"use client";

import { useState } from "react";

interface Scheme {
  id: number;
  title: string;
  description: string;
  publishDate: string;
  category: string;
}

const schemes: Scheme[] = [
  {
    id: 1,
    title: "Pradhan Mantri Awas Yojana-Urban 2.0",
    description:
      "Ambitious housing initiative by the Government of India, aimed at ensuring affordable housing for all in urban areas. Builds on success of earlier PMAY-U and introduces new features for improved outreach.",
    publishDate: "03/04/2025",
    category: "Housing",
  },
  {
    id: 2,
    title: "National Scholarship Portal",
    description:
      "Centralized portal for students to apply for scholarships provided by various government departments and ministries.",
    publishDate: "15/06/2024",
    category: "Education",
  },
  {
    id: 3,
    title: "Crop Insurance Scheme",
    description:
      "Provides financial support to farmers in case of crop failure due to natural calamities, pests, and diseases.",
    publishDate: "20/09/2024",
    category: "Farming",
  },
  {
    id: 4,
    title: "Women & Child Welfare Program",
    description:
      "Schemes for nutrition, education, and safety of women and children across India.",
    publishDate: "12/08/2024",
    category: "Women & Child",
  },
];

export default function CitizenDashboard() {
  const [filter, setFilter] = useState("All");
  const [activeTab, setActiveTab] = useState("schemes");

  const categories = ["All", "Housing", "Education", "Farming", "Women & Child"];

  const filteredSchemes =
    filter === "All"
      ? schemes
      : schemes.filter((scheme) => scheme.category === filter);

  return (
    <div className="p-6">
      {/* Tabs */}
      <div className="flex space-x-4 border-b mb-6">
        <button
          onClick={() => setActiveTab("schemes")}
          className={`pb-2 px-4 font-medium ${
            activeTab === "schemes"
              ? "border-b-4 border-indigo-600 text-indigo-600"
              : "text-gray-600 hover:text-indigo-500"
          }`}
        >
          Schemes
        </button>
        <button
          onClick={() => setActiveTab("chatbot")}
          className={`pb-2 px-4 font-medium ${
            activeTab === "chatbot"
              ? "border-b-4 border-indigo-600 text-indigo-600"
              : "text-gray-600 hover:text-indigo-500"
          }`}
        >
          Chatbot ðŸ¤–
        </button>
        <button
          onClick={() => setActiveTab("autofiller")}
          className={`pb-2 px-4 font-medium ${
            activeTab === "autofiller"
              ? "border-b-4 border-indigo-600 text-indigo-600"
              : "text-gray-600 hover:text-indigo-500"
          }`}
        >
          AutoFiller
        </button>
      </div>

      {/* Schemes Tab */}
      {activeTab === "schemes" && (
        <>
          <h1 className="text-3xl font-bold mb-6">Schemes</h1>

          {/* Filter Section */}
          <div className="flex items-center gap-2 bg-gray-100 p-4 rounded-lg mb-6">
            <span className="font-semibold">Filter Scheme category wise:</span>
            <select
              value={filter}
              onChange={(e) => setFilter(e.target.value)}
              className="border p-2 rounded-md"
            >
              {categories.map((cat) => (
                <option key={cat}>{cat}</option>
              ))}
            </select>
            <button className="bg-yellow-600 text-white px-4 py-2 rounded-md">
              Filter
            </button>
          </div>

          {/* Schemes List */}
          <div className="space-y-6">
            {filteredSchemes.map((scheme) => (
              <div
                key={scheme.id}
                className="border rounded-lg shadow-sm p-6 bg-white"
              >
                <h2 className="text-xl font-bold mb-2">{scheme.title}</h2>
                <p className="text-gray-600 mb-3">{scheme.description}</p>
                <p className="text-sm text-gray-500 mb-4">
                  PUBLISH DATE: {scheme.publishDate}
                </p>
                <button className="bg-yellow-700 hover:bg-yellow-800 text-white px-4 py-2 rounded-md">
                  View Details
                </button>
              </div>
            ))}
          </div>
        </>
      )}

      {/* Chatbot Tab */}
      {activeTab === "chatbot" && (
        <div>
          <h1 className="text-3xl font-bold mb-6">Chatbot ðŸ¤–</h1>
          <div className="border rounded-lg p-6 bg-gray-50 shadow">
            <p className="text-gray-600">
              [Chatbot UI will go here â€” you can integrate AI or FAQs later.]
            </p>
          </div>
        </div>
      )}

      {/* Autofiller Tab */}
      {activeTab === "autofiller" && (
        <div>
          <h1 className="text-3xl font-bold mb-6">AutoFiller</h1>
          <div className="border rounded-lg p-6 bg-gray-50 shadow">
            <p className="text-gray-600">
              [Autofiller UI will go here â€” you can integrate document uploads later.]
            </p>
          </div>
        </div>
      )}
    </div>
  );
}
